import { ProcessStepElement, ProcessStepElementBase } from '@jumoo/processing';
import { customElement, html, nothing } from '@umbraco-cms/backoffice/external/lit';
import { uSyncSnapshots } from '../../constants';

@customElement(uSyncSnapshots.steps.createReport)
export class uSyncSnapshotsCreateReportElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	render() {
		var snapshot = this.results?.results['snapshot'] as any;
		if (!snapshot) return nothing;
		if (snapshot.fileCount === 0) return this.renderNoChanges();

		return this.renderChanges(snapshot);
	}

	renderNoChanges() {
		return html`<uui-box .headline=${this.localize.term('uSyncSnapshots_noChanges')}>
			<umb-localize key="uSyncSnapshots_noChangesDescription"></umb-localize>
		</uui-box>`;
	}

	renderChanges(snapshot: any) {
		return html`
			<uui-box headline=${this.localize.term('uSyncSnapshots_report')}>
				<h2>${snapshot.name}</h2>
				<p>Snapshot contains ${snapshot.fileCount} files,</p>
			</uui-box>
		`;
	}
}

export default uSyncSnapshotsCreateReportElement;
